import React, {
  ChangeEventHandler,
  Dispatch,
  FunctionComponent,
  SetStateAction,
  useCallback,
  useMemo,
  useState,
} from "react";
import { useGetLocale } from "providers";
import clsx from "clsx";

import {
  Button,
  GetCoinImg,
  Input,
  ProgressiveModal,
  InputAdornment,
} from "components";
import { useCoins, useModalLocation } from "hooks";

import { useUser } from "providers/User";
import { ModalsProps } from "config/routes";
import ProgressFallback from "components/fallbacks/Progress";

import { modalsLoaderColor } from "sass/styleVariables";
import css from "./style.module.scss";

const userName = "Pasino";
const iconStyles = { bottom: 12, top: "auto", left: 10, zIndex: 1 };

const FEDeposit: FunctionComponent<ModalsProps> = ({ isOpen }) => {
  const intl = useGetLocale();
  const { user } = useUser();
  const {
    params: { coin },
    back,
  } = useModalLocation("fp-deposit");
  const { selectedCoin } = useCoins("selectedCoin");

  const [amount, setAmount] = useState("0.00000100");

  const callBackUrl = useMemo(
    () => `${process.env.REACT_APP_URL}/callback/fp`,
    [],
  );

  //#modal wallet
  const successUrl = `${process.env.REACT_APP_FRONT_URL}/modal/wallet?records=${
    coin || selectedCoin?.coin
  }`;

  const handleNumberChange = (
    value: string,
    setChange: Dispatch<SetStateAction<string>>,
  ) => {
    if (Number(value) || Number(value) === 0) {
      setChange(value);
    }

    if (!value) {
      setChange((0).toFixed(8));
    }
  };

  const close = () => {
    back();
  };

  const handleInputChange = useCallback<ChangeEventHandler<HTMLInputElement>>(
    ({ target }) => {
      handleNumberChange(target.value, setAmount);
    },
    [],
  );

  return (
    <ProgressiveModal open={isOpen} width={400} onClose={close}>
      <div className={css.box}>
        <h1 className={css.title}>
          {intl.formatMessage({ id: "fpDeposit.title" })}
        </h1>

        {user?.user_name ? (
          <>
            <div className={css.visibleWrapper}>
              <div className={css.coinWrapper}>
                <Input
                  startAdornment={
                    <InputAdornment position="start">
                      <GetCoinImg coin={coin} style={iconStyles} />
                    </InputAdornment>
                  }
                  className={clsx(css.visibleInput, css.coinInput)}
                  label={intl.formatMessage({ id: "fpDeposit.label" })}
                  value={amount}
                  onChange={handleInputChange}
                />
              </div>
            </div>

            <form
              action="https://faucetpay.io/merchant/webscr"
              method="post"
              className={css.form}>
              <input
                className={css.invisibleInput}
                type="text"
                readOnly
                name="merchant_username"
                value={userName}
              />

              <input
                className={css.invisibleInput}
                type="text"
                readOnly
                name="item_description"
                value="Pasino Deposit"
              />

              <input
                className={css.invisibleInput}
                type="text"
                readOnly
                name="amount1"
                value={amount}
              />

              <input
                className={css.invisibleInput}
                type="text"
                name="currency1"
                readOnly
                value={coin}
              />

              <input
                className={css.invisibleInput}
                type="text"
                name="currency2"
                readOnly
                value={coin}
              />

              <input
                className={css.invisibleInput}
                type="text"
                name="custom"
                readOnly
                value={user.user_name}
              />

              <input
                className={css.invisibleInput}
                type="text"
                readOnly
                name="callback_url"
                value={callBackUrl}
              />

              <input
                className={css.invisibleInput}
                type="text"
                readOnly
                name="success_url"
                value={successUrl}
              />

              <input
                className={css.invisibleInput}
                type="text"
                readOnly
                name="cancel_url"
                value={successUrl}
              />

              <Button className={css.button} type="submit">
                {intl.formatMessage({ id: "fpDeposit.button" })}
              </Button>
            </form>
          </>
        ) : (
          <ProgressFallback color={modalsLoaderColor} />
        )}
      </div>
    </ProgressiveModal>
  );
};

export default FEDeposit;
